.bookList {
  li {
    display: flex;

    .bookImage a {
      display: block;
      padding: 0.2rem;
      margin: 1rem 0.5rem 0 0;
      border: 1px solid #ccc;
      width: 6rem;

      img {
        width: 100%;
      }
    }

    .bookDiv {
      flex:1;
      h5 {
        margin:0.5rem 0;
      }

      .bookInfo {
        border-top: 1px solid #ccc;
        padding-top:0.5rem;
      }

      .intro {
        margin:0.5rem 0;
      }
    }

  }
}