@import "reset";
@import "bookArea";
@import "cateArea";
@import "centerArea";
@import "footer";
@import "bookItem";
@import "box";
@import "border";
@import "page";

html {
  background: #046cb1;
  font-size: 12px;
  font-family: Tahoma,Verdana;
}

hr {
  height: 0;
  margin-top: 1rem;
  margin-bottom: 1rem;
  border: 0;
  border-top: 1px solid rgba(0, 0, 0, 0.1);
}
//全局链接样式
a {
  color: #0449be;
  text-decoration: none;
  &:hover {
    text-decoration: underline;
  }
}
//use 1
.clearfx {
  clear: both;
}

.body-content {
  background: #fff;
//logo
  .brand {
    float: left;
    padding-bottom: 0.3rem;
    height: 65px;
    width: 358px;
    background: url("../images/top-logo.gif") no-repeat;
  }
  //搜索框
  .search-form {
    display: inline-block;
    margin: 1.5rem 0 0 3rem;
    float: left;
    height: 2.6rem;
    & > input[type='text'] {
      width: 260px;
      height: 2rem;
      border: 2px solid #299cd3;
    }
//搜索按钮
    & > button {
      margin-top: 0.3rem;
      height: 2rem;
      font-size: 1.2rem;
      background-color: #096DF3;
      border: #299cd3;
      color: white;
    }
  }
//导航条
  .nav ul {
    display: block;
    min-height: 2.5rem;
    background: #0977B7;
    padding-left: 1rem;
    margin: 0;
    border-radius: 0.2rem;

    li {
      display: inline-block;
    }

    a {
      color: #fff;
      height: 2.5rem;
      line-height: 2.5rem;
      display: inline-block;
      margin-right: 1rem;

      &:hover {
        color: yellow;
        text-decoration: none;
      }
    }
  }
}

@media screen and (max-width: 984px) {
    .body-content{
//logo
      .brand {
        width:100%;
        overflow: hidden;
      }
//搜索
      .search-form {
        margin:0;
        width:100%;
        & > input[type='text'] {
          width: 80%;
        }

      }
    }
}
