.cate-area {
  &>div {
    display: flex;
    flex-flow:row wrap;
  }

  .name {
    background:#cc5555;
    border-radius: 0.4rem;
    padding: 0.3rem 0.6rem;
    color: white;
    margin:0.3rem;
  }

  ul {
    display: flex;
    flex-flow:row wrap;
  }

  li {
    margin:0.3rem;

    a {
      background-color:#2192D1;
      color:white;
      display: inline-block;
      padding: 0.3rem 0.6rem;
      border-radius: 0.6rem;

      &:hover {
        text-decoration: none;
      }
    }
  }
}

@media screen and (min-width:789px) {
  .cate-area {
    font-size:12px;
  }
}