.center-area {
  .article {
    .cover {
      width: 100%;
    }
    .box-off {
      margin:1rem 0;
      border-top: 1px dashed #ccc;
    }

    .download-url-list {
      display: flex;
      flex-flow:row wrap;

      li {
        display: inline-block;
        margin: 0 1rem;
      }

      a {
        display: inline-block;
        padding: 0.3rem 1rem;
        border: 1px dotted #b0c7dd;

        &:hover {
          color:#FF6666;
        }
      }
    }
  }

  .info-list {
     dt {
      float: left;
      width: 100px;
      overflow: hidden;
      text-align: right;
      text-overflow: ellipsis;
      white-space: nowrap;
    }
     dd {
      margin-left: 120px;
    }
  }



}