.book-area {
  &>div {
    margin-top: 1rem;
  }

  .box {
    border:1px solid #b0c7dd;

    dt {
      color:#397CBE;
      background:#D7E7F7;
      padding:0.2rem 0.8rem;
      border-bottom: 1px solid #b0c7dd;
    }

    dd {
      padding: 0 0 0 0.3rem;
    }

    ul li {
      line-height: 1.7rem;
      height: 1.7rem;
      overflow: hidden;

      a {
        overflow: hidden;
        height: 1.7rem;
      }

      .date {
        float:right;
        width:3.5rem;
        color:#3d3e3f;
      }
    }
  }
}
